.menu {
  background-color: rgb(14, 13, 13);

  border-radius: 8px;
  position: absolute;
  top: 80px;
  right: 0;
  width: 200px;
  box-shadow: 0 1px 8px rgba(0, 0, 0, 0.3);
  opacity: 0;
  visibility: hidden;
  z-index: 1;
  transform: translateY(-25px);
  transition: opacity 0.3s ease, transform 0.4s ease, visibility 0.4s;
}
.menu.active {
  opacity: 1;
  visibility: visible;
  /* z-index: 14; */
  transform: translateY(-7%);
}
.menu ul {
  list-style: none;
  padding: 0;
  margin: 5% 0 10% 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

