/* hamburger menu */
.hammenu {
  display: none;
}

.wrapper {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;

  background: linear-gradient(
    -105deg,
    rgba(26, 24, 22, 1) 95%,
    rgba(39, 39, 43, 1) 100%
  );

  -webkit-clip-path: circle(0px at calc(100% - 45px) 45px);

          clip-path: circle(0px at calc(100% - 45px) 45px);
  transition: all 0.3s ease-in-out;
  z-index: 19;
}
#active:checked ~ .wrapper {
  -webkit-clip-path: circle(75%);
          clip-path: circle(75%);
}
.menu-btn {
  position: absolute;
  z-index: 20;
  right: 20px;
  /* left: 20px; */
  top: 25px;
  height: 50px;
  width: 50px;
  text-align: center;
  line-height: 50px;
  border-radius: 50%;
  font-size: 20px;
  color: #fff;
  cursor: pointer;
  background: var(--main-dark);
  transition: all 0.3s ease-in-out;
}
#active:checked ~ .menu-btn {
  background: #fff;
  color: #4158d0;
}
#active:checked ~ .menu-btn i:before {
  content: '\f00d';
}

.wrapper ul {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  list-style: none;
  text-align: center;
}
.wrapper ul li {
  margin: 15px 0;
}

