/* popup review box section */

.popup-box {
  position: fixed;
  width: 100%;
  height: 100vh;
  background: #00000050;
  top: 0;
  right: 0;
  z-index: 16;
}
.box {
  position: relative;
  width: 30rem;
  margin: 0 auto;
  min-height: 18rem;
  /* height: auto; */
  /* max-height: 70vh; */
  margin-top: calc(100vh - 80vh);
  background: rgb(66, 66, 66);
  border-radius: 4px;
  padding: 20px;
  border: none;
  overflow: auto;
}
.close-icon {
  content: "x";
  font-weight: bold;
  cursor: pointer;
  position: absolute;
  right: 5px;
  top: 5px;
  /* right: calc(15% - -100px); */
  /* top: calc(100vh - 85vh - 17px); */
  background: #fd4c4c;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  line-height: 20px;
  text-align: center;
  border: none;
  font-size: 20px;
}
textarea {
  margin-top: 2%;
  resize: none;
