/* loader component */

.loader {
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-top: 20%;
  width: 80px;
  height: 80px;
  padding-left: 0;
}
.loader:after {
  content: " ";
  display: block;
  width: 64px;
  height: 64px;
  margin: 8px;
  border-radius: 50%;
  border: 6px solid #f83636;
  border-color: #f83636 transparent;
  animation: lds-dual-ring 1.2s linear infinite;
}
.loader2 {
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-top: 5%;
  width: 80px;
  height: 80px;
  padding-left: 0;
}
.loader2:after {
  content: " ";
  display: block;
  width: 64px;
  height: 64px;
  margin: 8px;
  border-radius: 50%;
  border: 6px solid #f83636;
  border-color: #f83636 transparent;
  animation: lds-dual-ring 1.2s linear infinite;
}
@keyframes lds-dual-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

/* end of loader components */

